@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap');

.main {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 auto;
    position: relative;
}
.converter {
    max-width: 300px;
    margin-top: 10px;
}
.converter:nth-of-type(2) {
    margin-top: 40px;
}
p, h3, h5, h4 {
    margin: 0;
    padding: 0;
}
body {
    box-sizing: border-box;
    margin: 0;
    font-family: 'Montserrat', sans-serif;
    overflow: scroll;
    overflow-x: hidden;
}
.list_wrapper {
    display: flex;
    flex-direction: column;
    padding-left: 40px;
    max-width: 450px;
    padding-right: 100px;
}
.coin_list {
    text-align: center;
    transition: all 0.3s linear;
}
.coin_body {
    display: flex;
    text-align: left;
    justify-content: space-between;
    padding-right: 10px;
}
.coin_price {
    /* margin-right: 10px; */
    display: flex;
}
/* .coin_price p {
    lh10px
} */
.coin_body h4 {
    margin-bottom: 5px;
    margin-right: 5px;
}
.coin_cap {
    display: flex;
}
.coin {
    display: flex;
    max-width:100%;
    margin: 5px 0;
    border: 1px solid teal;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
    border-radius:5px 4px;
    cursor: pointer;
    transition: all 0.2s linear;
}
.coin:hover {
    transform: translate(4px, -4px);
}
.coin_order {
    display: flex;
    justify-content: center;
    align-items: center;
}

.coin_header img {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    width: 25px;
    height: 25px;
    margin-left: 10px;
}

.coin_header {
    display: flex;
}
.coin_header h3 {
    line-height: 25px;
}
.coin_order {
    flex: 1;
}
.coin_wrapper {
    flex: 7;
    text-align: center;
}

.converter {
    width: 400px;
    display: flex;
    flex-direction: column;
    position: relative;
    transition: all 0.3s linear;
}
.converter h3 {
    margin-bottom: 10px;
}
.converter label,
.calculator_header {
    display: block;
    font-size: 1.5em;
    font-weight: bold;
    color: teal;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.01);
}

input, label, select {
    margin-bottom: 10px;
}

.average_container {
    display: flex;
    margin-left: -30px;
}
.average_container__inner {
    display: flex;
    flex-direction: column;
    padding-right: 10px;
}
/* .average_container__inner :nth-of-type(1) {
    margin-right: 10px;
} */
.btn {
    border: none;
    border-radius: 4px;
    cursor: pointer;
    background-color: rgba(0, 128, 128, 0.8);
    color: white;
    padding: 10px 20px;
    margin-right: 10px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.4);
}
.btn:hover {
    background-color: rgba(0, 128, 128, 1);
}
.converter input::-webkit-outer-spin-button,
.converter input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}
.converter input {
    height: 20px;
    border-radius: 4px;
    border: 1px solid grey;
}
.converter input:focus {
    border: 1px solid rgba(0, 128, 128, 0.1);
}
select {
    font-family: 'Montserrat', sans-serif;
    padding: 2px 4px;
    border-radius: 4px;
    height: 24px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: relative;
    z-index: 1;
    font-size: 16px;
    background: url('../media/arr.png') no-repeat right 5px center / 11px auto;
}

::-webkit-scrollbar {
    background: transparent;
    border-radius: 2px;  
}

::-webkit-scrollbar-thumb {
    background: rgba(0, 128, 128, 0.2); 
    border-radius: 2px;  
}

::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 102, 128, 0.247); 
}

.average_container input {
    max-width: 188px;
}

.crypto_input::placeholder {
    font-family: 'Montserrat', sans-serif;
    padding-left: 2px;
    color:rgba(0, 0, 0, 0.5);
    font-size: 16px;
}

h3 {
    color: rgba(0, 102, 128, 1);
    text-shadow: 1px 1px 0.1px rgba(0, 0, 0, 0.01);
}

.btn_container {
    margin-top: 10px;
    display: flex;
    padding-bottom: 20px;
}

input:focus,
select:focus {
    outline: 1px solid teal;
}

.reverse_btn {
    font-family: 'Montserrat', sans-serif;
    position: absolute;
    top: 196px;
    right: 0;
    border: 1px solid teal;
    border-radius: 4px;
    background: transparent;
    height: 24px;
    padding: 0 22px;
    cursor: pointer;
    transition: all 0.2s ease;
    color: teal;
}

/* .reverse_btn::after {
    content: '';
    position: absolute;
    top: 2px;
    right: 2px;
    background-image: url('../media/arr_right.svg');
    z-index: 100;
    height: 18px;
    width: 18px;
    transform: rotate(180deg);
}

.reverse_btn::before {
    content: '';
    position: absolute;
    top: 2px;
    left: 2px;
    background-image: url('../media/arr_right.svg');
    z-index: 100;
    height: 18px;
    width: 18px;
} */

.reverse_btn:hover {
    background: rgba(0, 102, 128, 0.5);
    box-shadow: 1px 1px 7px rgba(0, 0, 0, 0.3);
    border: 1px solid transparent;
}
.filters {
    display: flex;
    justify-content: center;
}
.filters button {
    flex: 1;
    font-family: 'Montserrat', sans-serif;
}
.filters button:nth-of-type(2),
.filters button:nth-of-type(3) {
    padding-top: 8px;
}
.filters button:nth-of-type(3) {
    margin-right: 0;
}
.search_form {
    display: flex;
    margin-top: 20px;
}
.search {
    height: 30px;
    border-radius: 4px;
    border: 1px solid teal;
    padding-left: 5px;
    /* margin-right: 10px; */
    flex: 4;
}
.search::placeholder {
    font-family: 'Montserrat', sans-serif;
    display: block;
    position: relative;
    top: 2px;
    font-size: 16px;
}

.search_form button {
    height: 34px;
    margin-right: 0;
}
.features_container {
    margin-top: 76px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-right: 40px;
}
h2 {
    color: teal;
    margin: 10px auto;
}
.price_pos {
    flex: 3;
    text-align: right;
    line-height: 24px;
    padding-right: 8px;
    padding-top: 2px;
    color: teal;
}
.price_neg {
    flex: 3;
    text-align: right;
    line-height: 24px;
    padding-right: 8px;
    padding-top: 2px;
    color: red;
}
.header {
    font-size: 30px;
    display: flex;
    margin: 10px 0;
}
.sort_header {
    font-size: 20px;
}
.crypto_info {
    display: flex;
    flex-direction: column;
    max-width: 800px;
    margin: 50px auto;

}
.crypto_info li {
    list-style: none;
}
.crypto_info a {
    color: teal;
}
.crypto_data {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.crypto_data div {
    flex: 1;
}
.crypto_data ul {
    padding-left: 0;
}

.crypto_header {
    display: flex;
    width: 400px;
    margin: 0 auto;
    justify-content: center;
}

.crypto__descr {
    flex-direction: column;
    text-align: justify;
    margin-bottom: 20px;
}
.crypto__descr p {
    margin-top: 20px;
    text-indent: 25px;
}

.crypto_header h2 {
    margin: 0;
    line-height: 50px;
    margin-right: 20px;
}
.crypto_header img {
    width: 50px;
    height: 50px;
}
.table {
    display: flex;
    flex-direction: column;
    border: 1px solid teal;
    flex-basis: 160px;
    flex-shrink: 3;
    border-radius: 4px;
}

.thead {
    display: flex;
    border-bottom: 1px solid teal;
    background: rgba(0, 128, 128, 0.5);
    text-align: center;
}

.thead .td{
    cursor: pointer;
}
.thead .td:hover {
    background: rgba(0, 128, 128, 0.7);
}

.tbody {
    display: flex;
    flex-direction: column;
}

.tbody div {
    display: flex;
}

.td {
    padding-top: 10px;
    min-height: 30px;
    border-right: 1px solid teal;
    flex: 1;
    justify-content: center;
    text-align: center;
}
/* .td a {
    width: 100%;
} */

.td:last-of-type {
    border-right: none;
}

.tr {
    border-bottom: 1px solid teal;
}

.tbody .tr:last-of-type{
    border-bottom: none;
}
.market_data {
    transition: all 0.2s ease;
}
.market_data h4 {
    display: flex;
    justify-content: space-between;
}
.market_data h4 span:last-child {
    color: teal;
}

.crypto_info h3 {
    font-size: 22px;
}
.crypto_info h3:last-of-type {
    margin-bottom: 10px;
}
.ath {
    position: relative;
    /* border-bottom: 2px dotted teal; */
}
.ath::after {
    content: '';
    position: absolute;
    top: 17px;
    left: 0;
    width: 33px;
    height: 2px;
    border-top: 2px dashed rgba(0, 128, 128, 0.6);
}

.ath_hint {
    position: absolute;
    top: -40px;
    left: 0;
    width: 140px;
    height: 30px;
    background: rgba(0, 128, 128, .8);
    display: none;
    z-index: 100;
    border-radius: 20px 4px 20px 4px;
    line-height: 30px;
    text-align: center;
    color: white;
    font-size: 14px;
    font-weight: 300;
}
.ath_hint:after {
    box-sizing: border-box;
    content: '';
    position: absolute;
    bottom: -20px;
    left: 6px;
    width: 20px;
    height: 20px;
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-top: 10px solid rgba(0, 128, 128, .8);
}
.ath:hover > .ath_hint {
    display: block;
}
.coin {
    text-decoration: none;
    color: black;
}
.list_wrapper {
    max-width: 450px;
}
.paginator_wrapper {
    text-align: center;
    margin-top: 5px;
}

.paginator_wrapper .btn {
    width: 100%;
    height: 30px;
    line-height: 30px;
    padding-top: 0;
}

.features {
    display: none;
    position: absolute;
    top: 20px;
    min-width: 140px;
    height: 30px;
    margin-left: 350px;
    padding-left: 6px;
    padding-right: 20px;
    background-color: rgba(0, 128, 128, 0.8);
    color:white;
    font-family: 'Montserrat', sans-serif;
}
.features option {
    height: 30px;
}
select.features option:hover {
    box-shadow: 0 0 10px 100px #198d8d inset; 
}
.current_price {
    margin-top: 10px;
    margin-bottom: 10px;
}
.btn_back,
.btn_top {
    display: none;
}

footer {
    display: flex;
    flex-direction: column;
    max-width: 850px;
    margin: 20px auto 0 auto;
    text-align: justify;
    padding: 20px 40px;
    background: rgba(128,128,128, 0.2);
    border-radius: 4px;
}
footer span {
    color: red;
}

.spinner {
    display: block;
    margin: 0 auto;
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.chart_container iframe {
    min-width: 100%;
    display: flex;
    max-width: 100vw;
    
}

@media screen and (max-width:1042px) {
    .features {
        display: flex;
    }
    .list_wrapper {
        padding-left: 100px;
        padding-right: 50px;
    }
    .features_container {
        padding-left: 50px;
        margin-top: 80px;
        padding-right: 50px;
        display: none;
    }
    .coin_body {
        flex-direction: column;
    }
    .average_container {
        margin-left: -30px;

    }
    .average_container input {
        max-width: 100%;
    }
    .average_container__inner {
        padding-right: 10px;
    }
    /* .btn {
        padding: 10px 10px;
    } */
}




@media screen and (max-width:500px) {
    .iframe {
        margin-left: 20px;
        margin-right: 20px;
    }
    .chart_container{
        margin-left: 10px;
    }
    .crypto_info {
        overflow-x: hidden;
        margin-top: 70px;
    }
    .crypto_info h3 {
        margin-left: 20px;
    }
    .crypto_info>a {
        display: none;
    }
    .coin_header h3 {
        text-align: left;
    }
    .average_container {
        margin-left: 0;
    }
    .average_container__inner {
        
        max-width: 140px;
        justify-content: flex-end;
    }
    .list_wrapper {
        padding-left: 35px;
        padding-right: 30px;
        margin-bottom: 10px;
    }
    .features {
        top: 55px;
        margin-left: 180px;
    }
    .features_container {
        margin-top: 100px;
    }
    .crypto__descr p {
        margin-left: 20px;
        margin-right: 20px;
    }
    .crypto_data {
        flex-direction: column;
        margin-left: 20px;
        margin-right: 20px;
    }
    .market_data h4:last-of-type span:last-of-type {
        text-align: right;
        width: 33%;
        margin-top: 20px;
    }
    .table {
        margin-left: 20px;
        margin-right: 20px;
    }
    /* .table .tr .td:last-of-type,
    .table .thead .td:last-of-type {
        display: none;
    } */
    .table .tr .td,
    .table .thead .td {
        width: 20%;
        display: inline;
        word-wrap:break-word;
    }
    .btn_back,
    .btn_top {
        display: block;
        position: fixed;
        height: 30px;
        margin-left: 20px;
        width: 350px;
        line-height: 30px;
        padding: 0 20px;
        font-family: 'Montserrat', sans-serif;
    }
    .btn_back:hover,
    .btn_top:hover {
        background: rgba(0, 128, 128, 1);
    }
    .btn_top {
        bottom: 10px;
    }
    .btn_back {
        top: 10px;
        display: block;
    }
    .btn_back a {
        color: white;
        text-decoration: none;
    }
    .btn_back::before {
        content: '';
        background-image: url('../media/arr.png');
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
        height: 10px;
        width: 10px;
        top: 10px;
        left: 32px;
        transform: rotate(90deg);
    }
    .btn_top::before {
        content: '';
        background-image: url('../media/arr.png');
        position: absolute;
        background-repeat: no-repeat;
        background-size: contain;
        height: 10px;
        width: 10px;
        top: 10px;
        right: 134px;
        transform: rotate(180deg);
    }
}